.root {
  position: relative;
  height: 300px;
  width: 300px;
  transform: rotate(-75deg);
  border-radius: 50%;
}

.root::after {
  content: "";
  position: absolute;
  height: 38%;
  width: 38%;
  top: 31%;
  left: 31%;
  background: #252b33;
  box-shadow: inset -3px 0px 5px black;
  border-radius: 50%;
}

.circle {
  position: absolute;
  divst-style: none;
  height: 0;
  padding: 0;
  /* clip-path: circle(35% at 50% 50%); */
  clip-path: circle(48% at 50% 50%);
}

.major {
  padding-top: 100%;
  width: 100%;
  background-color: #5aadd8;
}

.minor {
  padding-top: 70%;
  width: 70%;
  left: 15%;
  top: 15%;
  background-color: #1c4c70;
}


.circle > div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  clip-path: polygon(50% 50%, 100% 50%, 93.30127% 25%);
  z-index: 5;
  background-color: rgba(255, 255, 255, 0);
}


.major > div::after {
  right: 0%;
  top: 33%;
}

.minor > div::after {
  right: 0%;
  top: 33%;
}

.circle div:nth-child(1) {
  transform: rotate(0deg);
}

.circle div:nth-child(2) {
  transform: rotate(30deg);
}

.circle div:nth-child(3) {
  transform: rotate(60deg);
}

.circle div:nth-child(4) {
  transform: rotate(90deg);
}

.circle div:nth-child(5) {
  transform: rotate(120deg);
}

.circle div:nth-child(6) {
  transform: rotate(150deg);
}

.circle div:nth-child(7) {
  transform: rotate(180deg);
}

.circle div:nth-child(8) {
  transform: rotate(210deg);
}

.circle div:nth-child(9) {
  transform: rotate(240deg);
}

.circle div:nth-child(10) {
  transform: rotate(270deg);
}

.circle div:nth-child(11) {
  transform: rotate(300deg);
}

.circle div:nth-child(12) {
  transform: rotate(330deg);
}

.circle div:hover {
  background-color: rgba(250, 230, 95, 0.6);
}

/* || labels for major and minor keys */
.circle div::after {
  position: absolute;
  width: 22%;
  text-align: center;
  font-size: 1.5em;
  color: white;
  text-shadow: 0.4vmin 0.4vmin 1vmin black;
}



