.root {
  margin-top: 20px;
  width: 300px;
  position: relative;
}

.root > div {
  display: inline-block;
  border: 1px solid #b7b7b7;
  border-radius: 5px;

}

.black {
  background: black;
  position: absolute;
  margin: 0px 0 0px -10px;
  box-shadow: 2px 2px 3px #000000b0;
  border: 0!important;
  height: 75px;
  width: 25px;
}
.black.active {
  background: #1c4c70;
}

.white {
  background: white;
  height: 120px;
  width: calc(100%/7);
  box-sizing: border-box;
}

.white.active {
  background: #5aadd8;
  border-color: #1c4c70;
}
